.button {
    padding: 10px 20px;
    border-radius: 6px;
    background-color: #9e7f38;
    transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
    display: inline-block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .Button:hover,
  .Button:focus {
    background-color: #303f9f;
  }

  .btnDiv {
    display: flex;
  }